.backdrop-header {
  width: 100%;
  height: 100%;
  z-index: 10000;
  background-color: rgba(0, 0, 0, .2);
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

