}

.backdrop-header.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop-header.is-hidden .modal-header {
  visibility: hidden;
  opacity: 0;
  transform: translate(-50%, -50%)scale(.9);
}

.modal-open {
  overflow: hidden;
}

.modal-header {
  width: calc(100% - 30px);
  max-width: 450px;
  background-color: var(--bgColorSecondary);
  background-image: url("about-bg-mob.1471cb83.png");
